@media screen and (max-width: 1170px) {
    .header {
        .top-nav-toggle { display: block; }
        .top-nav {
            display: none;
            &.open {
                display: block;
                position: absolute;
                top: 50px;
                background: #fff;
                // border-radius: 5px;
                @include g-box-shadow(0, 0, 20px, rgba(0,0,0,.4));
                width: auto;
                min-width: 170px;
                z-index: 10;

                ul {
                    display: block;
                    &.sub-nav {
                        display: none;
                    }
                }
                li {
                    padding: 0 5px;
                    a {
                        color: #000;
                        &:hover {
                            color: #56cc9d;
                        }
                    }
                    &.active a {
                        color: #56cc9d;
                    }
                }
            }
        }
    }
} // end max-width: 1170px



